<template>
  <div>
    <GroupSelect placeholder='xxx' :data-source='options' v-model="value"></GroupSelect>
    {{value}}
  </div>
</template>

<script>
import GroupSelect from './GroupSelectCom.vue'
export default {
  components: {
    GroupSelect,
  },
  data () {
    return {
      options: [{
        label: '',
        checked: false,
        options: [{
          value: 'Shanghai',
          label: ''
        }, {
          value: 'Beijing',
          label: ''
        }]
      }, {
        label: '',
        checked: false,
        options: [{
          value: 'Chengdu',
          label: ''
        }, {
          value: 'Shenzhen',
          label: ''
        }, {
          value: 'Guangzhou',
          label: ''
        }, {
          value: 'Dalian',
          label: ''
        }]
      }],
      value: [],
    }
  },
  methods: {

  }
}

</script>



