<template>
  <div>
    <el-divider content-position="left">:value="msg" @input="msg = $event.target.value"</el-divider>
    <input type="text" :value="msg" @input="msg = $event.target.value">
    <p>msg:{{msg}}</p>
    <el-divider content-position="left">childModel</el-divider>
    <childModel v-model="child" @change="handleChange"></childModel>
    <p>child:{{child}}</p>
    <el-divider content-position="left">model</el-divider>
    <muSelect v-model="selValues" placeholder='attr....' :data-source='list' :props="{label:'name', value:'id'}" @change="handleSelChange"></muSelect>
    <p>{{selValues}}</p>

    <el-divider content-position="left">model v-bind='$attrs' v-on="$listeners"</el-divider>
    <listeners v-model="listener" @change="handleListenChange" @blur="testBlur" clearable @clear=handleClear placeholder=''></listeners>
    <p>listener:{{listener}}</p>
  </div>
</template>

<script>
import childModel from './child-model'
import muSelect from './mu-select'
import listeners from './listeners'
export default {
  components: {
    childModel,
    muSelect,
    listeners
  },
  data() {
    return {
      listener: '',
      list: [{
        id: '1',
        name: ''
      }, {
        id: '2',
        name: ''
      }, {
        id: '3',
        name: ''
      }, {
        id: '4',
        name: ''
      }, {
        id: '5',
        name: ''
      }],
      selValues: [],
      msg: '',
      child: ''
    }
  },
  methods: {
    handleListenChange(val) {
      console.log('listener:', val)
    },
    handleClear() {
      this.$message.success('...')
    },
    testBlur() {
      this.$message.success('el-input blur...')
      console.log('blur')
    },
    handleSelChange(val) {
      console.log(val)
    },
    handleChange(val) {
      console.log('val: ', val);
    }
  }

}
</script>

<style lang="less" scoped>

</style>



