<template>
  <div class="wp">
    <el-row type="flex">

 <el-col >
    <el-button type="primary" size="mini" @click="goAnchor('#anchor1')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor2')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor3')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor4')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor5')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor6')"></el-button>
  <el-button type="primary" size="mini" @click="goAnchor('#anchor7')"></el-button>
 </el-col>
    </el-row>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor1" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor2" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor3" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor4" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor5" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor6" :style="{'background':getColor()}"></div>
  <div style="height:200px" :style="{'background':getColor()}"></div>
  <div style="height:100px" id="anchor7" :style="{'background':getColor()}"></div>
  </div>
</template>

<script>
export default {
  methods: {
    goAnchor(selector) { // selectorid#anchor14
      document.querySelector(selector).scrollIntoView({
        behavior: "smooth"
      });
    },
    getColor() {
      this.r = Math.floor(Math.random() * 255);
      this.g = Math.floor(Math.random() * 255);
      this.b = Math.floor(Math.random() * 255);
      return 'rgba(' + this.r + ',' + this.g + ',' + this.b + ',0.8)'
    }
  }
}
</script>

<style lang="less" scoped>
.wp{
  height: 3000px;
  position: relative;
  .el-row{
    position: fixed;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
  }
  div{

  }
}
</style>



